<template>
  <div class="detail">
    <div class="detail-swipe" @click="showControl">
      <van-swipe class="detail-swipe" @change="onChange" :show-indicators="false" :initial-swipe="current">
        <van-swipe-item class="detail-swipe" v-for="(thumb,index) in thumb" :key="index">
          <lazy-component class="image">
            <van-image class="image" :src="thumb" fit="contain" />
          </lazy-component>
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="header" v-show="showControlView">
      <van-icon name="cross" size="24"  @click="goBack" />
      <span>{{ current + 1 }}/{{thumb.length}}</span>
    </div>

    <van-tabs  v-show="showControlView" class="tabs-image" @change="onChange" v-model="current" scrollspy sticky>
      <van-tab  v-for="(thumb,index) in thumb" :key="index">
        <template #title>
          <lazy-component class="thumb-wrap">
            <van-image class="thumb" :src="thumb" fit="fill" />
          </lazy-component>
        </template>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import { Tabs, Tab, Icon, Swipe, SwipeItem, Image } from "vant";

export default {
  components: {
    [Tabs.name]: Tabs,
    [Tab.name]: Tab,
    [Icon.name]: Icon,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Image.name]: Image
  },

  data() {
    return {
      showControlView: true,
      current: 0,
      thumb: [
        "https://img.yzcdn.cn/public_files/2017/10/24/e5a5a02309a41f9f5def56684808d9ae.jpeg",
        "https://img.yzcdn.cn/public_files/2017/10/24/e5a5a02309a41f9f5def56684808d9ae.jpeg",
        "https://img.yzcdn.cn/public_files/2017/10/24/e5a5a02309a41f9f5def56684808d9ae.jpeg",
        "https://img.yzcdn.cn/public_files/2017/10/24/e5a5a02309a41f9f5def56684808d9ae.jpeg",
        "https://img.yzcdn.cn/public_files/2017/10/24/e5a5a02309a41f9f5def56684808d9ae.jpeg",
        "https://img.yzcdn.cn/public_files/2017/10/24/e5a5a02309a41f9f5def56684808d9ae.jpeg",
        "https://img.yzcdn.cn/public_files/2017/10/24/e5a5a02309a41f9f5def56684808d9ae.jpeg",
        "https://img.yzcdn.cn/public_files/2017/10/24/e5a5a02309a41f9f5def56684808d9ae.jpeg",
        "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1587026021938&di=4049147fb84839229ba5ff49c0f6e39f&imgtype=0&src=http%3A%2F%2Fb-ssl.duitang.com%2Fuploads%2Fitem%2F201607%2F04%2F20160704171940_mV2A8.jpeg",
        "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1587025973552&di=69d5cda1615676de0f10b153587f5478&imgtype=0&src=http%3A%2F%2Fe.hiphotos.baidu.com%2Fexp%2Fw%3D500%2Fsign%3D91d211582b34349b74066e85f9eb1521%2F7dd98d1001e9390195c07d7c78ec54e737d196b6.jpg",
        "https://img.yzcdn.cn/public_files/2017/10/24/1791ba14088f9c2be8c610d0a6cc0f93.jpeg"
      ]
    };
  },

  methods: {
    onChange(index) {
      this.current = index;
    },
    showControl() {
      this.showControlView = !this.showControlView;
    },

    goBack() {
         this.$router.go(-1);
    }
  }
};
</script>

<style lang="less">
.detail {
  width: 100%;
  background: #000;
  height: 100vh;
  &-swipe {
    width: 100%;
    height: 100vh;
    .image {
      height: 100vh;
    }
  }
  .header {
    position: fixed;
    display: flex;
    height: 40px;
    background: rgba(0, 0, 0, 0.1);
    top: 0;
    left: 0;
    right: 0;
    font-size: 14px;
    flex-direction: row;
    padding: 0px 16px;
    justify-content: space-between;
    align-items: center;
    color: #fff;
  }
  .tabs-image {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    .thumb-wrap {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      height: 40px;
    }
    .thumb {
      width: 40px;
      height: 40px;
    }
  }
}
</style>
